/* Theme CSS Variables */
:root {
  /* Accent Color - Default Yellow */
  --accent-color: #FBBF24;
  --accent-color-rgb: 251, 191, 36;

  /* Font Sizes */
  --base-font-size: 16px;

  /* Density/Spacing */
  --spacing-unit: 1rem;

  /* Animations */
  --animation-duration: 200ms;
}

/* Font size variations */
[data-font-size="small"] {
  --base-font-size: 14px;
}

[data-font-size="medium"] {
  --base-font-size: 16px;
}

[data-font-size="large"] {
  --base-font-size: 18px;
}

/* Density variations */
[data-density="compact"] {
  --spacing-unit: 0.75rem;
}

[data-density="comfortable"] {
  --spacing-unit: 1rem;
}

[data-density="spacious"] {
  --spacing-unit: 1.5rem;
}

/* Animation control */
[data-animations="false"] * {
  transition: none !important;
  animation: none !important;
}

/* Apply font size to html for rem scaling */
html {
  font-size: var(--base-font-size);
}

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    background-color: #F7F9FC;
    transition: background-color 0.3s ease;
  }

  body {
    font-family: 'Inter', sans-serif;
    background-color: #F7F9FC;
    color: #111827;
    transition: background-color 0.3s ease, color 0.3s ease;
    min-height: 100vh;
  }

  /* Dark mode styles */
  .dark html {
    background-color: #111827;
  }

  .dark body {
    background-color: #111827;
    color: #F9FAFB;
  }

  .dark {
    color-scheme: dark;
  }

  /* Ensure root element has dark background with smooth transition */
  #root {
    background-color: #F7F9FC;
    min-height: 100vh;
    transition: background-color 0.3s ease;
  }

  .dark #root {
    background-color: #111827;
    min-height: 100vh;
  }

  /* Dark mode for common elements */
  .dark .bg-white {
    background-color: #374151 !important;
  }

  .dark .bg-gray-50 {
    background-color: #1F2937 !important;
  }

  .dark .bg-gray-100 {
    background-color: #374151 !important;
  }

  .dark .bg-gray-200 {
    background-color: #4B5563 !important;
  }

  /* Text colors - ensure visibility in dark mode */
  .dark .text-gray-900,
  .dark .text-gray-800 {
    color: #F9FAFB !important;
  }

  .dark .text-gray-700 {
    color: #E5E7EB !important;
  }

  .dark .text-gray-600 {
    color: #D1D5DB !important;
  }

  .dark .text-gray-500 {
    color: #D1D5DB !important;
  }

  .dark .text-gray-400 {
    color: #D1D5DB !important;
  }

  /* Improved color contrast in dark mode */
  .dark .text-blue-700,
  .dark .text-blue-600 {
    color: #93C5FD !important;
  }

  .dark .text-blue-800 {
    color: #BFDBFE !important;
  }

  .dark .text-green-700,
  .dark .text-green-600 {
    color: #86EFAC !important;
  }

  .dark .text-green-800 {
    color: #BBF7D0 !important;
  }

  .dark .text-orange-700,
  .dark .text-orange-600 {
    color: #FDBA74 !important;
  }

  .dark .text-orange-800 {
    color: #FED7AA !important;
  }

  .dark .text-yellow-700,
  .dark .text-yellow-600 {
    color: #FDE047 !important;
  }

  .dark .text-yellow-800 {
    color: #FEF08A !important;
  }

  .dark .text-red-700,
  .dark .text-red-600 {
    color: #FCA5A5 !important;
  }

  .dark .text-red-800 {
    color: #FECACA !important;
  }

  .dark .text-purple-700,
  .dark .text-purple-600 {
    color: #C4B5FD !important;
  }

  .dark .text-purple-800 {
    color: #DDD6FE !important;
  }

  /* Additional dark mode improvements for better contrast */
  .dark .text-gray-200 {
    color: #E5E7EB !important;
  }

  .dark .text-gray-300 {
    color: #D1D5DB !important;
  }

  /* Cyan/Teal colors for dark mode */
  .dark .text-cyan-200,
  .dark .text-cyan-300 {
    color: #A5F3FC !important;
  }

  /* Ensure all badge text is visible in dark mode */
  .dark .text-blue-200 {
    color: #BFDBFE !important;
  }

  .dark .text-green-200 {
    color: #BBF7D0 !important;
  }

  .dark .text-orange-200 {
    color: #FED7AA !important;
  }

  .dark .text-yellow-200 {
    color: #FEF08A !important;
  }

  .dark .text-red-200 {
    color: #FECACA !important;
  }

  .dark .text-purple-200 {
    color: #DDD6FE !important;
  }

  .dark .text-black {
    color: #F9FAFB !important;
  }

  /* Ensure labels and spans are visible */
  .dark label,
  .dark span,
  .dark p,
  .dark h1,
  .dark h2,
  .dark h3,
  .dark h4,
  .dark h5,
  .dark h6 {
    color: inherit;
  }

  .dark label {
    color: #E5E7EB;
  }

  /* Border colors */
  .dark .border-gray-200,
  .dark .border-gray-300 {
    border-color: #4B5563 !important;
  }

  /* Form elements */
  .dark input,
  .dark textarea,
  .dark select {
    background-color: #374151;
    color: #F9FAFB;
    border-color: #4B5563;
  }

  .dark input::placeholder,
  .dark textarea::placeholder {
    color: #9CA3AF;
  }

  /* Select dropdowns */
  .dark select option {
    background-color: #374151;
    color: #F9FAFB;
  }

  /* Hover states */
  .dark .hover\:bg-gray-50:hover {
    background-color: #4B5563 !important;
  }

  .dark .hover\:bg-gray-100:hover {
    background-color: #4B5563 !important;
  }

  .dark .hover\:text-gray-900:hover {
    color: #F9FAFB !important;
  }

  /* Buttons and interactive elements */
  .dark button {
    color: inherit;
  }

  .dark button.bg-white {
    color: #111827;
  }

  /* Cards and containers */
  .dark .shadow,
  .dark .shadow-sm,
  .dark .shadow-md,
  .dark .shadow-lg {
    box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.1);
  }

  /* Disabled states */
  .dark input:disabled,
  .dark textarea:disabled,
  .dark select:disabled {
    background-color: #1F2937;
    color: #6B7280;
    opacity: 0.6;
  }

  /* Focus states */
  .dark input:focus,
  .dark textarea:focus,
  .dark select:focus {
    border-color: #60A5FA;
    ring-color: #60A5FA;
  }
}

@layer utilities {
  .selection-bg-primary {
    @apply selection:bg-yellow-200;
  }
}

/* Custom animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-4px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fadeIn {
  animation: fadeIn 0.2s ease;
}

/* Scroll animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes float {

  0%,
  100% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(-20px);
  }
}

@keyframes pulse-slow {

  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }
}

@keyframes shimmer {
  0% {
    background-position: -1000px 0;
  }

  100% {
    background-position: 1000px 0;
  }
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(100px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce-subtle {

  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

/* Animation classes */
.animate-fade-in-up {
  animation: fadeInUp 0.6s ease-out forwards;
}

.animate-fade-in-down {
  animation: fadeInDown 0.6s ease-out forwards;
}

.animate-fade-in-left {
  animation: fadeInLeft 0.6s ease-out forwards;
}

.animate-fade-in-right {
  animation: fadeInRight 0.6s ease-out forwards;
}

.animate-scale-in {
  animation: scaleIn 0.5s ease-out forwards;
}

.animate-float {
  animation: float 3s ease-in-out infinite;
}

.animate-pulse-slow {
  animation: pulse-slow 3s ease-in-out infinite;
}

.animate-shimmer {
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  background-size: 1000px 100%;
  animation: shimmer 2s infinite;
}

.animate-slide-in-up {
  animation: slideInUp 0.8s ease-out forwards;
}

.animate-bounce-subtle {
  animation: bounce-subtle 2s ease-in-out infinite;
}

/* Delay classes for staggered animations */
.animation-delay-100 {
  animation-delay: 0.1s;
}

.animation-delay-200 {
  animation-delay: 0.2s;
}

.animation-delay-300 {
  animation-delay: 0.3s;
}

.animation-delay-400 {
  animation-delay: 0.4s;
}

.animation-delay-500 {
  animation-delay: 0.5s;
}

.animation-delay-600 {
  animation-delay: 0.6s;
}

.animation-delay-700 {
  animation-delay: 0.7s;
}

.animation-delay-800 {
  animation-delay: 0.8s;
}

/* Scroll reveal */
.scroll-reveal {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.scroll-reveal.revealed {
  opacity: 1;
  transform: translateY(0);
}

/* Hover effects */
.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

/* Gradient animation */
@keyframes gradient-shift {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

