/* Ensure gradient buttons have white text in both light and dark modes */
button[class*="bg-gradient-to"] {
  color: white !important;
}

button[class*="bg-gradient-to"] * {
  color: white !important;
}

/* Force dark text for goal buttons in light mode */
.goal-button-light {
  color: rgb(17, 24, 39) !important;
}

.dark .goal-button-light {
  color: white !important;
